<template>
  <main class="thingsThatShouldBeHiddenpqbe">
    <div class="tablequestion">
    <table class="table">
             <thead class=" thingsThatShouldBeHiddenpqbe">
        <tr>
          <th scope="col" class="col-4">제목</th>
          <th scope="col" class="col-1">공개 여부</th>
          <th scope="col" class="col-1"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(list, i) in state.lists" :key="(list, i)">
            <td> 
              <RouterLink :to="{name: 'questionread', params:{id:list.questionid}}"  style="color:rgba(6, 90, 92, 0.9); text-decoration:none; font-weight:bold;" >
                {{ list.title }}
              </RouterLink>
            </td>
            <td>
                <div class="form-check form-switch form-check-inline" >
                  <input v-if="!list.questionPublic" class="form-check-input text-end" type="checkbox" role="switch" @change="sendValues(list.questionid, list.questionPublic, list.title, list.content)" >
                  <input v-else class="form-check-input text-center" type="checkbox" role="switch" checked @change="sendValues(list.questionid, list.questionPublic, list.title, list.content)">
                </div>
            </td>

          </tr>
        </tbody>
      </table>


    </div>


  </main>

</template>


<script>
import {useRouter} from 'vue-router';
import { reactive, ref } from 'vue'
import axios from 'axios'
import { onMounted } from 'vue'


export default {
  name: "TheQuestion",

  setup() {
    onMounted(() => { loginHandler(); })

    const router=useRouter();
    const questionPublic = ref(true)

    const value = ref(false)
    const state = reactive({
      lists: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });

    const values = reactive({
      questionid: '',
      questionPublic:'',
      title:'',
      content:'',
    })

    const sendValues = (questionid,questionPublic,title,content) => {
      values.questionid = questionid
      values.questionPublic = questionPublic
      values.title = title,
      values.content = content,
      changeSwitchOption()
    };

    const loginHandler = async () => {
      const url = "http://43.201.60.125/api/questionlistbyemail"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }

    const changeSwitchOption = async () => {
      console.log("modify QuestionPublic launch")
      const url = "http://43.201.60.125/api/switchquestionpulic"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        questionid: values.questionid,
        title: values.title,
        content: values.content,
        questionPublic: !values.questionPublic,
      }
      const response = await axios.post(url, body, { headers })
      if (response.status === 200) {
        alert('modify successfully')
      } else {
        alert('fail')
      }
    }

    //delete Modal
    const deleteModal = () => {
      ElMessageBox.confirm(
        'Do you really want to delete your Question?',
        'Warning',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
          deleteQuestion()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: 'Delete canceled',
          })
        })
    }


    const deleteQuestion = async () => {
      const url = 'http://43.201.60.125/api/deleteQuestion'
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { questionid: values.questionid }
      console.log("delete Question... launch" + values.questionid)
      await axios.post(url, body, { headers }).then(function (res) {
        console.log("delete user after axios")
        if (res.status === 200) {
          console.log(`question${values.questionid} is deleted`);
        }
      })
    }


    return { questionPublic, value, state, sendValues, values, deleteModal, loginHandler, changeSwitchOption, deleteQuestion, onMounted }

  }

};


</script>

<style scoped>
.checkbox-in-the-middle{
  padding-left: 5em;
 }
.container {
  display: block;
  text-align: left;
  width: 500px;
  height: 500px;
}

label {
  margin: 10px 0;
}

textarea {
  width: 400px;
  height: 180px;
}
</style>